import React, { Component } from 'react'
import { Switch, Route, Redirect} from 'react-router-dom';
import Header from './components/Header/Header';
import LoginComponent from './components/Login/Login';
import BishopComponent from './components/Bishop/Bishop';
import AdminComponent from './components/Admin/Admin';
import DisciplerComponent from './components/Discipler/DisciplerPage';

import { HOME, DISCIPLER, ADMIN, BISHOP, LOGIN } from './components/constants/pageUrls';

class routes extends Component {
    render() {
        return (
            <div>
                <Header />
                 <Switch>
                    <Route exact path={HOME}>
                        <LoginComponent/> 
                    </Route>
                    <Route exact path={DISCIPLER}>
                        <DisciplerComponent />                       
                    </Route>
                    <Route exact path={ADMIN}>
                        <AdminComponent />                       
                    </Route>
                    <Route exact path={BISHOP}>
                        <BishopComponent />                       
                    </Route>
                    <Redirect to={HOME} />
                 </Switch>
            </div>
        )
    }
}


export default routes;